.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: rgb(0 0 0 / 30%);
  visibility: hidden;
  opacity: 0;

  transition: visibility 0.3s, opacity 0.3s linear;
}
